import recipes from "./data/data.json";
import getRecipeTemplate from "./components/RecipeComponent";

// VARIABLES DECLARATIONS
const sectionRecipes = document.getElementById("sectionRecipes") as HTMLElement,
  recipeClasses =
    "bg-white max-w-sm w-full max-h-3xl h-full rounded-md" as string;

async function addRecipes(): Promise<void> {
  for (const element of recipes) {
    const recipeArticle = document.createElement("article");

    // Use dynamic import for images
    const recipePictureModule = await import(
      `./assets/recipesPictures/Recette${element.id}.jpg`
    );
    const recipePicture = recipePictureModule.default;

    recipeArticle.className = recipeClasses;
    recipeArticle.innerHTML = getRecipeTemplate(
      element.id,
      element.time,
      recipePicture
    );
    sectionRecipes.append(recipeArticle);
  }
}

addRecipes();
